.nav {
  li {
    display: inline-block;
    @include notlast {
      margin-right: 1rem; } }
  a {
    @include tr(color .3s);
    @include hover {
      color: $accent; } }
  &__item-current {
    color: $accent; }

  &--footer {
    li {
      display: block;
      @include notlast {
        margin-right: 0;
        margin-bottom: rem(12); } } } }
